	import {
		get,
		post,
		config
	} from '@/utils/api.js';
	export default {
		name: "qdGetPrize",
		data() {
			return {
				imgUrl: config.staticUrl,
				ipx: false,
				
				latitude:'', //地理位置
				longitude:'',
				speed:'',
				accuracy :'',
				
				openid: '',
				getqp: false, //抽中青啤有一套
				gethb: false, // 抽中红包
				overOne: false, //抽中金额超过1元
				getNull: false,
				
				userScanMonthCount:'', // 累计次数
				needScan:'',   // 获得1个抽奖机会需要的瓶数
				hasNum:'', // 剩余抽奖次数
				
				animationData:{} ,//旋转动画
				runDegs:'',
				awardsList:[
					'谢谢参与',
					'青啤有一套',
					'随机微信红包', 
					'谢谢参与', 
					'青啤有一套',
					'随机微信红包',
				],
				
				earnMoney:'', //抽中金额
				surplusMoney:'',// 账户余额
			}
		},
		onUnload(){
			this.animationData={}; //清空数据
		},
		async onLoad(options) {
			// let awardsList = ['随机微信红包', '谢谢参与', '随机微信红包', '青啤有一套', '谢谢参与', '青啤有一套'];
			// this.initAwardList(awardsList);
		},
		onShow() {
			var _this = this;
			//调用定位方法
			_this.getUserLocation();
			
			// 动画
			var animation = wx.createAnimation({
			  duration: 4000,
			  timingFunction: 'ease'
			})
			
			this.animation = animation
			console.log(this.animation);
		},
        
		/**
		 * 用户点击右上角分享
		 */
		onShareAppMessage(res) {
			if (res.from === 'button') { // 来自页面内分享按钮
				console.log(res.target)
			}
			return {
				title: '青岛啤酒',
				path: '/pages/home/home',
				imageUrl: this.imgUrl + 'share.png'
			}
		},
        mounted(){
			this.initData();
		},
		methods: {
			// 初始化 转盘抽奖用户信息
			async initData(){
				var params = {
					"openid": this.$store.state.openid, 
					"projectServerName": 'liaoning',
				};
				var jo = await post('/turnaroundDraw/queryTurnaroundDrawUser', params);
				console.log(jo); //用户抽奖信息结果 reply result replyTime
				if(jo.result.code == 0 ){ // 服务正常
					if (jo.result.businessCode == 0) { //正常  
						this.userScanMonthCount = String(jo.reply.userScanMonthCount); // 用户本月扫码累计次数
						this.needScan = String(jo.reply.turnaroundScan) ; // 获得1个抽奖机会需要的瓶数
						this.hasNum = String(jo.reply.turnaroundSurplusCount);  //抽奖次数
					} else {
						this.userScanMonthCount = '0'; // 用户本月扫码累计次数
						this.needScan = '0' ; // 获得1个抽奖机会需要的瓶数
						this.hasNum = '0';  //抽奖次数
						// uni.showModal({
						// 	title: '尊敬的用户',
						// 	content: jo.result.msg
						// })
					}
				} else {  //服务失败
					uni.showModal({
						title: '尊敬的用户',
						content: jo.result.msg
					})
				}
			},
			cj(){  //点击抽奖
				if(this.hasNum > 0){ //可抽
					this.getLottery(); //抽奖
				} else {
					uni.showModal({
						title: '尊敬的用户',
						content: '您当前抽奖次数为0'
					})
				}
			},
			bigPrize(){ //立即领取
				uni.navigateTo({
					url:'../mine/prizeList'
				})
			},
			toMine(){ //to个人中心
				uni.switchTab({
					url:'../mine/mine'
				})
			},
			oneMore(){ //中红包——再抽一次
				this.gethb = false;
			},
			/**
			  * 初始化抽奖
			*/
		  //   initAwardList: function(list) {
				// // 绘制转盘
				// var awardsList = [];
				// var angle = 360 / list.length;
				// for (var i = 0; i < list.length; i++) {
				//   awardsList.push({
				// 	degree: {
				// 	  item: `${i * angle}deg`,
				// 	  line: `${(i + 0.5) * angle}deg`,
				// 	},
				// 	award: list[i],
				//   })
				// }
		  //   },
		  
			/**
			 * 开始抽奖
			*/
			async getLottery(){
				this.getqp = false;
				this.gethb = false;
				this.overOne = false;
				this.getNull = false;
				
				uni.showLoading({
					title: '抽奖中...',
				})
				var params = {
					"openid": this.$store.state.openid, 
					"projectServerName": 'liaoning',
					"longitude":this.longitude,  //经度
					"latitude":this.latitude     //纬度
				};
				var jo = await post('/turnaroundDraw/exchangetTurnaroundDraw', params);
				console.log(jo); //抽奖结果 reply result replyTime
				uni.hideLoading();
				if(jo.result.code == 0 ){ // 服务正常
				    // this.hasNum = this.hasNum - 1; //可用的次数减少一次 
					// 转动圈数
					let runNum = 6;
					if (jo.result.businessCode == 0) { //正常  
					    this.hasNum = String(jo.reply.surplusCount) ; // 剩余次数
						this.userScanMonthCount = String(jo.reply.userScanMonthCount); // 本月扫码累计次数
						this.needScan = String(jo.reply.turnaroundScan); //一个抽奖机会所需瓶数
						if(jo.reply.prizeType == 'P' || jo.reply.prizeType == 'p'){ //抽中实物奖
						    // 获奖序号
						    var awardIndex = 2 || 5;
						    // 旋转抽奖
						    this.runDegs = this.runDegs || 0;
						    this.runDegs = this.runDegs + (360 - this.runDegs % 360) + (360 * runNum - awardIndex * (360 / this.awardsList.length));
						    // 开始转动
							
							this.animation.rotate(this.runDegs).step();
							this.animationData = this.animation.export()
							// 中奖提示
							setTimeout(() => {
							  console.log('抽中了奖品: ' + awardIndex)
							  this.getqp = true; 
							}, 4000 + 300)
						} else if (jo.reply.prizeType == '0' && jo.reply.earnMoney>0){ //抽到红包
							// 获奖序号
							var awardIndex = 3 || 6;
							// 旋转抽奖
							this.runDegs = this.runDegs || 0;
							this.runDegs = this.runDegs + (360 - this.runDegs % 360) + (360 * runNum - awardIndex * (360 / this.awardsList.length));
							// 开始转动
							// 动画
							this.animation.rotate(this.runDegs).step();
							this.animationData = this.animation.export()
							// 中奖提示
							setTimeout(() => {
							  console.log('抽中了奖品: ' + awardIndex)
							  
							  this.earnMoney = jo.reply.earnMoney; //抽中金额
							  this.surplusMoney = jo.reply.surplusMoney ;//账户余额
							  this.gethb = true;
							  if(jo.reply.earnMoney>=1){
							  	this.overOne = true;
							  } else {
							  	this.overOne = false;
							  }
							}, 4000 + 300)
						} else {  //无中奖
							// 获奖序号
							var awardIndex = 1 || 4;
							// 旋转抽奖
							this.runDegs = this.runDegs || 0;
							this.runDegs = this.runDegs + (360 - this.runDegs % 360) + (360 * runNum - awardIndex * (360 / this.awardsList.length));
							// 开始转动
							// 动画
							this.animation.rotate(this.runDegs).step();
							this.animationData = this.animation.export()
							// 中奖提示
							setTimeout(() => {
							  console.log('抽中了奖品: ' + awardIndex)
							  this.getNull = true;
							}, 4000 + 300)
						}
					} else if(jo.result.businessCode == 8){ //没抽到谢谢惠顾
					
					    this.hasNum = String(jo.reply.surplusCount) ; // 剩余次数
					    this.userScanMonthCount = String(jo.reply.userScanMonthCount); // 本月扫码累计次数
					    this.needScan = String(jo.reply.turnaroundScan); //一个抽奖机会所需瓶数
						
						// 获奖序号
						var awardIndex = 1 || 4;
						// 旋转抽奖
						this.runDegs = this.runDegs || 0;
						this.runDegs = this.runDegs + (360 - this.runDegs % 360) + (360 * runNum - awardIndex * (360 / this.awardsList.length));
						// 开始转动
						// 动画
						this.animation.rotate(this.runDegs).step();
						this.animationData = this.animation.export()
						// 中奖提示
						setTimeout(() => {
						  console.log('抽中了奖品: ' + awardIndex)
						  this.getNull = true;
						}, 4000 + 300)
					} else {
						uni.showModal({
							title: '尊敬的用户',
							content: jo.result.msg
						})
					}
				} else {  //服务失败
					uni.showModal({
						title: '尊敬的用户',
						content: jo.result.msg
					})
				}
			},
			closeGetQp() {
				this.getqp = false;
			},
			closeGetNull() {
				this.getNull = false;
			},

			//定位方法
			getUserLocation: function() {
				var _this = this;
				wx.getSetting({
					success: (res) => {
						// res.authSetting['scope.userLocation'] == undefined    表示 初始化进入该页面
						// res.authSetting['scope.userLocation'] == false    表示 非初始化进入该页面,且未授权
						// res.authSetting['scope.userLocation'] == true    表示 地理位置授权
						if (res.authSetting['scope.userLocation'] != undefined && res.authSetting['scope.userLocation'] != true) {
							//未授权
							wx.showModal({
								title: '请求授权当前位置',
								content: '是否授权您的地理位置',
								success: function(res) {
									if (res.cancel) {
										//取消授权
										// wx.showToast({
										// 	title: '拒绝授权',
										// 	icon: 'none',
										// 	duration: 1000
										// })
									} else if (res.confirm) {
										//确定授权，通过wx.openSetting发起授权请求
										wx.openSetting({
											success: function(res) {
												if (res.authSetting["scope.userLocation"] == true) {
													wx.showToast({
														title: '授权成功',
														icon: 'success',
														duration: 1000
													})
													//再次授权，调用wx.getLocation的API
													_this.geo();
												} else {
													wx.showToast({
														title: '授权失败',
														icon: 'none',
														duration: 1000
													})
												}
											}
										})
									}
								}
							})
						} else if (res.authSetting['scope.userLocation'] == undefined) {
							//用户首次进入页面,调用wx.getLocation的API
							_this.geo();
						} else {
							console.log('授权成功')
							//调用wx.getLocation的API
							_this.geo();
						}
					}
				})
			},

			// 获取定位城市
			geo: function() {
				var _this = this;
				wx.getLocation({
					type: 'wgs84',
					success: function(res) {
						_this.latitude = res.latitude
						_this.longitude = res.longitude
						_this.speed = res.speed
						_this.accuracy = res.accuracy
					},
					fail:function(){
						// wx.showToast({
						// 	title: '已拒绝授权',
						// 	icon: 'none',
						// 	duration: 1000
						// })
					}
				})
			},
		}
	}
